* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.app {
  padding: 50px;
  height: 100vh;
  background-image:  url('Images/bg.jpg');
  background-size: cover;   
}

h1 { 
  color: white;
}
 span {
      display: block;
      color: darken(white, 20%);
      font-size: 14px;
      font-style: italic;
      height: 14px;
  }


.note-form {
  display: flex;
  flex-direction: column;
  width: 200px;
}
  .note-form textarea {
      font-family: Arial, Helvetica, sans-serif;
      border: 1px solid lightgrey;
      min-width: 100%;
      max-width: 100%;
      height: 100px;
      padding: 10px;
      outline: none;
  }

  button {
      width: 100%;
      background-color: #fff27f;
      cursor: pointer;
      padding: 10px; 
      border: 0;
      outline: none;
  }
      button:hover {
          background-color: rgb(206, 148, 247);
          color: rgb(251, 251, 252);
      }
  


.note {
  position: absolute;
  cursor: pointer;
  background-image: url('Images/post-it-note.png');
  background-size: cover;
  top: 100px;
  left: 300px;
  height: 200px;
  width: 200px;
  padding: 20px;
}

  .text {
      font-family: Arial, Helvetica, sans-serif;
      height: 100%;
      width: 100%;
      white-space: pre-wrap;
      overflow-y: auto;
  }

  .close {
      width: 15px;
      height: 15px;
      position: absolute;
      right: 15px;
      transition: transform .3s ease;
  }
      close:hover {
          transform: scale(1.6);
      }
  
